/*user*/
@connect(
	state => state.user,
	{login}
)
@imoocForm
class Login extends React.Component {
	constructor(props) {
		super(props);
		/*this.state = {
			user: '',
			pwd: ''
		}*/
		/*
		* 1.constructor
		* 2.Onclick,
		* 3.onClick,constructorbind(this)
		* */
		this.register = this.register.bind(this);
		this.handleLogin = this.handleLogin.bind(this);
	}

	/*
		* 1.:,
		* 2.,login
		* 3.this.props.history.push();
		* console.log(this.props);
		* route4
		* */
	register() {
		this.props.history.push('/register')
	}

	/*, auth.redux.js */
	handleLogin() {
		this.props.login(this.props.state);
	}

	/*
	* */


	render() {
		return (
			<div>
				{(this.props.redirectTo && this.props.redirectTo !== '/login') ? <Redirect to={this.props.redirectTo}/> : null}
				<Logo/>
				<WingBlank>
					<List>
						{this.props.msg ? <p className='error-msg'>{this.props.msg}</p> : null}
						<InputItem onChange={v => this.props.handleChange('user', v)}></InputItem>
						<InputItem type='password' onChange={v => this.props.handleChange('pwd', v)}></InputItem>
					</List>
					<WhiteSpace/>
					<Button onClick={this.handleLogin} type="primary"></Button>
					<WhiteSpace/>
					<Button onClick={this.register} type="primary"></Button>
				</WingBlank>
			</div>
		)
	}
}

export default Login


