/*
* title:,
* 2.redux
* @connectboosinfo,redux.js
* */
import React from 'react'
import {Redirect} from 'react-router-dom'
import {
	NavBar,
	InputItem,
	TextareaItem,
	Button
} from 'antd-mobile'
import {connect} from 'react-redux'
import {update} from '../../redux/user.redux'
import AvatarSelector from '../../component/avatar-selector/avatar-selector'

/*
* connectstate.reducer,action
*
* */
@connect(
	state => state.user,
	{update}
)
class Boosinfo extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			title: '',
			desc: '',
			company: '',
			money: ''
		}
	}

	onChange(key, v) {
		this.setState({
			[key]: v
		})
	}

	render() {
		const path = this.props.location.pathname;
		const redirect = this.props.redirectTo;

		return (
			<div>
				{/**/}
				{redirect && redirect !== path ? <Redirect to={this.props.redirectTo}/> : null}
				<NavBar mode="dark">Boss</NavBar>
				<AvatarSelector
					selectAvatar={(imgName) => {
						this.setState({
							avatar: imgName
						})
					}}
				/>
				<InputItem onChange={(v) => this.onChange('title', v)}></InputItem>
				<InputItem onChange={(v) => this.onChange('company', v)}></InputItem>
				<InputItem onChange={(v) => this.onChange('money', v)}></InputItem>
				<TextareaItem rows={2} count={100} title="" autoHeight onChange={(v) => this.onChange('desc', v)}/>
				<Button
					onClick={() => {
						/*redux*/
						this.props.update(this.state);
					}}
					type='primary'></Button>
			</div>
		)
	}
}

export default Boosinfo


