/*
* 1.index.jsProviderstorestore,BrowserRouterRoute
*2.reducer.jscombineReducers({reducer})index.js----createStore(reducer);
*3.composethunkwindow.devToolsExtension,devToolsExtension,statecompose
*4.Routepath="/",exact,
*
*
* */
import React from 'react'
import ReactDom from 'react-dom'
import {createStore, applyMiddleware, compose} from 'redux'
import thunk from 'redux-thunk'
import {Provider} from 'react-redux'
import {BrowserRouter, Route, Switch} from 'react-router-dom'

/*start*/
import Login from './container/login/login'
import Register from './container/register/register'
import Authroute from "./component/authroute/authroute";
import BoosInfo from './container/boosinfo/boosinfo';
import GeniusInfo from './container/geniusinfo/geniusinfo';
import Dashboard from './component/dashboard/dashboard'
import Chat from './component/chat/chat'
/*end*/
import reducers from './reducers'
import './config'
import './index.css'


/**/
const store = createStore(reducers, compose(
	applyMiddleware(thunk),
	window.devToolsExtension ? window.devToolsExtension() : f => f
));
/**/
/*function Boos() {
	return <h2>Boss</h2>
}
function Dashboard(){
return <h2>Dashboard</h2>
}*/
/*
*  app
* boos genuis me msg 4
*1.Route path,
*switch:
* */
ReactDom.render(
	(<Provider store={store}>
		<BrowserRouter>
			{/*login,register*/}
			<div>
				{/*,,*/}
				<Authroute/>
				<Switch>
					<Route path='/boosinfo' component={BoosInfo}/>
					<Route path='/geniusinfo' component={GeniusInfo}/>
					<Route path='/login' component={Login}/>
					<Route path='/register' component={Register}/>
					{/*
					 *,
					 * ,:this.props.history.push(`/chat/${v._id}`);
					 * :const userid = this.props.match.params.user;
					 * */}
					<Route path='/chat/:user' component={Chat}/>
					<Route component={Dashboard}/>
				</Switch>
			</div>
		</BrowserRouter>
	</Provider>)
	, document.getElementById('root')
);



