/*
* 1.class export default.
* 2.:View,@connect,
*
* */

import React from 'react'
import Logo from '../../component/logo/logo'
/*UI*/
import {List, InputItem, Radio, Button} from 'antd-mobile'
import {connect} from 'react-redux'
/**/
import {Redirect} from 'react-router-dom'

import {register} from "../../redux/user.redux";
import imoocForm from "../../component/imooc-form/imooc-form"

/*state.reducer action*/
@connect(
	state => state.user,
	{register}
)
@imoocForm
class Register extends React.Component {
	constructor(props) {
		super(props);
		/**/
		/*
		* state
		* */
		/*this.state = {
			user: '',
			pwd: '',
			repeatpwd: '',
			type: 'genius'    //boos  zou redux
		};*/
		this.handleRegiter = this.handleRegiter.bind(this);  //
	}

	/* register dispatch(action) 
	* user.redux.js actionreducer
	* */
	componentDidMount() {
   	this.props.handleChange('type','genius');
	}

	handleRegiter() {
		this.props.register(this.props.state);
	}

	/*
	* 
	* */

	/*	handleChange(key, val) {
			this.setState({
				[key]: val      /!*[]*!/
			});
		}*/

	render() {
		const RadioItem = Radio.RadioItem;
		return (
			<div>
				{this.props.redirectTo ? <Redirect to={this.props.redirectTo}/> : null}
				<Logo/>
				<h2></h2>
				{this.props.msg ? <p className='error-msg'>{this.props.msg}</p> : null}
				<List>
					<InputItem onChange={v => this.props.handleChange('user', v)}></InputItem>
					<InputItem type='password' onChange={v => this.props.handleChange('pwd', v)}></InputItem>
					<InputItem type='password' onChange={v => this.props.handleChange('repeatpwd', v)}></InputItem>
					{/**/}
					<RadioItem checked={this.props.state.type == 'genius'} onChange={v => this.props.handleChange('type', 'genius')}>
						
					</RadioItem>
					<RadioItem checked={this.props.state.type == 'boos'} onChange={v => this.props.handleChange('type', 'boos')}>
						Boos
					</RadioItem>
					<Button type='primary' onClick={this.handleRegiter}></Button>
				</List>
			</div>
		)
	}
}

/**/
export default Register






